@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{background-color:#fff}.App{text-align:center;background-color:#fff}h1{color:#000;font-weight:bold}.NavBar{display:flex;justify-content:space-between;background-color:#fff;padding:1% 5% 1% 5%;border-bottom:1px solid black}.NavBar .title{align-self:center;font-family:"Montserrat",sans-serif;font-weight:normal}.NavBar .links{display:flex;align-items:center}.NavBar a{color:#000;padding:5px;text-decoration:none}.NavBar a:hover{color:#fff;background-color:#FAE6B1}.Footer{display:flex;justify-content:space-evenly;background-color:#fff;padding:10px}.Footer .links{display:flex;align-items:center}.Footer a{color:#000;padding:5px;text-decoration:none}.Footer a:hover{color:#fff;background-color:#FAE6B1}

